<template>
    <div id="app">
        <h1>Bitcoin Now</h1>
        <div class="container">
            <div class="items USD">
                <h2><sup>&#36;</sup> {{ USD.split('.')[0] }}</h2>
                <h3>US Dollar</h3>
            </div>
            <div class="items GBP">
                <h2><sup>&pound;</sup> {{ GBP.split('.')[0] }}</h2>
                <h3>Pound Sterling</h3>
            </div>
            <div class="items EUR">
                <h2><sup>&euro;</sup> {{ EUR.split('.')[0] }}</h2>
                <h3>Euro</h3>
            </div>
        </div>
        <div class="footer">
            <p>Powered by <a href="https://www.coindesk.com/price/">CoinDesk</a> </p>
        </div>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'app',
    data: () => ({
        USD: '',
        GBP: '',
        EUR: '',
        errors: [],
    }),

    created() {
        axios.get(`https://api.coindesk.com/v1/bpi/currentprice.json`)
        .then(response => {
            this.USD = response.data.bpi.USD.rate;
            this.GBP = response.data.bpi.GBP.rate;
            this.EUR = response.data.bpi.EUR.rate;
        })
        .catch(e => {
            this.errors.push(e)
        })
    },

    computed: {

    }
}
</script>

<style lang="scss">
html {font-size: 1em;}

body {
    font-family: 'Lato', sans-serif;
    font-weight: 300;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
}

p {margin-bottom: 1.3em;}

h1, h2, h3, h4 {
  font-weight: inherit;
  line-height: 1.2;
}

h1 {
  margin-top: 0;
  font-size: 3.998em;
  font-family: 'Alex Brush', cursive;
}

h2 {font-size: 2.827em;}

h3 {font-size: 1.999em;}

a {
    color: #364F6B;
    transition: color 0.3s;
    text-decoration: none;

    &:hover {
        color: #3FC1C9;
    }
}

.container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.items {
    margin: 20px;
    flex-basis: 18rem;
    color: #FFF;
    padding: 40px;
}

.USD {
    background-color: #5d5485;
}

.GBP {
    background-color: #d96784;
}

.EUR {
    background-color: #ed948b;
}

.footer {
    margin-top: 30px;
}

</style>



